export default {
	data() {
		return {
			height: '',
			tab_items: [
				"待处理",
				"订单管理",
				"商品管理",
				"我的"
			],
			merchantId:'',
			orders:'',
			shopname:'',
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.shopname=e.data.name;
				that.Record();
			}
		});
	},
	onReady() {
		this.getTopheight();
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
				}
			})
		},
		Record: function() {
			let that = this;
			uni.request({
				url: this.serveipd + '/api/merchant/order/amountRecord',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
				},
				success: res => {
					this.orders = res.data.data;
					console.log(this.orders);
				},
				fail: () => {},
				complete: () => {}
			});
		},
	},
}
